.rentEquipo{
  height: auto;
  width: 100%;
  margin-bottom: 20px;
}

.rentEquipoItems{
  display: flex;
  flex-flow: row wrap;
  gap: 10px;
  justify-content: center;
}


.historias{
  display: flex;
  flex-direction: column;
  align-items: center;
  height: auto;
  background-color: rgba(20, 20, 20, 97%);
}

#historias-h, #clientes-h{
