.nosotros{
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 1px solid black;
}

.nosotrosFigure{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 0;

}

.nosotrosImg{
  width: 80%;
  border: 2px solid;
}

.nosotrosImgTxt{
  font-size: 1.1rem;
  background-color: rgba(20, 20 , 20, 95%);
  width: 81%;
  padding: 7px 0;
  text-align: center;
  color: ghostwhite;
}

.nosotrosLeyenda{
  background-color: rgba(20, 20 , 20, 95%);
  color: ghostwhite;
  width: 80%;
  padding: 20px 16px;
  font-size: 1.2rem;
  border-radius: 7px;
  margin-bottom: 30px;

}

#teamMembers{
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-bottom: 20px;
}

.teamFigure{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 70px;
}

.teamImg{
  width: 112px;
  margin: 0;
  border: 2px solid;

}

.teamImgTxt{
  font-size: 1.1rem;
  background-color: rgba(20, 20 , 20, 95%);
  width: 116px;
  padding: 7px 0;
  text-align: center;
  color: ghostwhite;
}

.contacto, #ubicacion{
  width: 100%;
}

.contactoKeyVal, .ubicacionKeyVal{
  margin-bottom: 15px;
  padding: 0
}

.contactoKey, .ubicacionKey{
  display: inline-block;
  width: 40%;  
  color: rgba(20, 20 , 20, 95%);
  font-size: 1rem;
  padding: 10px 10px;  
  text-align: end;
}

.contactoVal, .ubicacionVal{
  display: inline-block;
  width: 43%;  
  color: rgba(20, 20 , 20, 95%);
  font-size: 1rem;
  padding: 10px 10px;
}

#locationMap{
  width: 300px;
  height: 220px;
  margin-bottom: 20px;
}

.clientes{
  background-color: rgba(20, 20, 20, 95%);
  border-bottom: 1px solid black;

}

.clientesLogos{
  display: flex;
  flex-flow: row wrap;
  height: auto;
  justify-content: center;
  gap: 15px 10px;
  padding-bottom: 60px;
}

.clientImg{
  width: 40%;
  height: 100px;
}


